import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { filter, take } from 'rxjs';

import { selectIsLoadingPosts, selectPosts } from '@store/feed/feed.selectors';
import { AppStateInterface } from '@store/app/app-state.interface';
import { getPostsAction } from '@store/feed/feed.actions';

@Component({
  selector: 'app-home',
  standalone: false,
  templateUrl: './home.component.html',
  styleUrl: './home.component.scss',
})
export class HomeComponent implements OnInit {
  count: number = 4;
  posts$ = this.store.select(selectPosts);
  isLoading$ = this.store.select(selectIsLoadingPosts);

  constructor(private store: Store<AppStateInterface>) {}

  ngOnInit() {
    this.store.dispatch(getPostsAction({ count: this.count }));
  }

  onScrollingFinished() {
    this.isLoading$
      .pipe(
        filter((isLoading) => !isLoading),
        take(1),
      )
      .subscribe(() => {
        this.store.dispatch(getPostsAction({ count: this.count }));
      });
  }
}
