import { createSelector } from '@ngrx/store';

import { FeedStateInterface } from './feedState.interface';
import { AppStateInterface } from '@store/app/app-state.interface';

export const feedFeatureSelector = (state: AppStateInterface): FeedStateInterface => state.feed;
export const selectIsLoadingPosts = createSelector(feedFeatureSelector, (state) => state.isLoading);
export const selectPosts = createSelector(feedFeatureSelector, (state) => state.posts);
